import d3 from 'd3v3';
import newStore from './newStore';
import { OPTS_RATIO } from '../constants';
import { pokemon } from '../pokemon';
import TreeMap from '../components/TreeMap'

function rstr() {
  var text = "";
  var p = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

  for (var i = 0; i < 5; i++)
    text += p.charAt(Math.floor(Math.random() * p.length));

  return text;
}

function rcolor() {
  var p = ['cornflowerblue', 'darkgoldenrod', 'aqua', 'firebrick', 'darksalmon',
           'lavender', 'khaki', 'ivory'];
  return p[Math.floor(Math.random() * p.length)];
}

function get_initial_tree_structure(data) {
  const node_names = new Set();
  for (let entity of data) {
    for (let node in entity) {
      if (entity.hasOwnProperty(node) && node != 'name') {
        node_names.add(node);
      }
    }
  }
  return Array.from(node_names).map(function(x) {
    return {id: x, content: x}
  });
}

// a little function to help us with reordering the result
const tree_structure_reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);
  return result;
};

function initViz(ctx) {
  return new TreeMap(
    pokemon,
    ctx.vizdiv,
    {
      w: ctx.viz_width,
      h: ctx.height,
      navbar_height: 20,
      aggregator_function: 'AVG',
      stroke_on: true,
      tree_structure: ctx.tree_structure || ['type1'],
      filter_selections: new Map([
        ['type', new Map([['all', true]])],
      ]),
      render_depth: 1,
      color_attr: 'attack',
      fill_color: function(x, attribute) {
        if (x.type == 'leaf') {
          if (x.entity[attribute]) {
            return ctx.fill_color(x.entity[attribute]);
          } else {
            return '#bbb';
          }
        } else {
          const descendants = TreeMap.gather_all_descendants(x);
          let vals = descendants
            .filter(x => x[attribute])
            .map(x => x[attribute]);
          let avg = vals.reduce((a, b) => a + parseInt(b), 0) / vals.length;
          return ctx.fill_color(avg);
          return avgColor(
            descendants
              .filter(x => x[attribute])
              .map(x => ctx.fill_color(x, attribute)));
        }
      }
    }
  );
}

const reducers = {
  WINDOW_UPDATE: (state, action) => {
    this.viz_width = window.innerWidth
      ? window.innerWidth * (1 - OPTS_RATIO) : 0;
    this.height = window.innerHeight || 0;
      this.gradients = create_gradients(pokemon, 'attack');
    this.fill_color = create_fill_color_function(this.gradients);
    this.vizdiv = action.payload.vizdiv;
    this.treemap = initViz(this);
    return Object.assign({}, state, {
      viz_width: this.viz_width,
      opts_width: window.innerWidth ? window.innerWidth * OPTS_RATIO : 0,
      height: this.height,
      tree_structure: get_initial_tree_structure(pokemon),
    });
  },
  UPDATE_STRUCTURE: (state, action) => {
    const new_structure = tree_structure_reorder(
        state.tree_structure, action.payload.src, action.payload.dst);
    this.tree_structure = new_structure.map(x => x.content).slice(0, 3);
    this.treemap = initViz(this);
    return Object.assign({}, state, {
      color: ['limegreen', 'burlywood'][Math.floor(Math.random() * 2)],
      tree_structure: new_structure,
    });
  },
};

function create_gradients(data, attr) {
  const max = data.map(a => a[attr]).reduce((a, b) => Math.max(a, b), 0);
  const min = data.map(a => a[attr]).reduce((a, b) => Math.min(a, b), 0);
  return [
    {'domain_start': min, 'domain_end': max,
     'color_start': 'darkred', 'color_end': 'white'}
  ]
}

function create_fill_color_function(color_gradients) {
  const color_scales = color_gradients.map(cg => {
    return d3.scale
      .linear()
      .domain([cg.domain_start, cg.domain_end])
      .range([cg.color_start, cg.color_end])
      .interpolate(d3.interpolateHsl);
  });
  return function(val) {
    if (val == undefined) {
      return '#bbb';
    }
    for (let i = 0; i != color_gradients.length; i++) {
      if (
        val >= color_gradients[i].domain_start &&
        val <= color_gradients[i].domain_end
      ) {
        return color_scales[i](val);
      }
    }
    return '#bbb';
  }.bind(this);
}

